
#app {
	font-family: Avenir, Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 60px;

	main{
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-template-areas:
			'creature-list creature-details creature-details';
	}
}
